form input[type=radio], form input[type=checkbox] {
  margin: 3px;
  accent-color: #000;
}
form input[type=radio]:focus, form input[type=checkbox]:focus {
  filter: drop-shadow(0px 0px 1px #0997FF);
}
form input[type=radio]:disabled, form input[type=checkbox]:disabled {
  accent-color: var(--light-020, #D3D4D6);
}
form input[type=radio]:disabled + label, form input[type=checkbox]:disabled + label {
  color: var(--light-020, #D3D4D6);
}
form select:not([multiple]) {
  appearance: none;
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="1em" viewBox="0 0 448 512"><path d="M241 337c-9.4 9.4-24.6 9.4-33.9 0L47 177c-9.4-9.4-9.4-24.6 0-33.9s24.6-9.4 33.9 0l143 143L367 143c9.4-9.4 24.6-9.4 33.9 0s9.4 24.6 0 33.9L241 337z"/></svg>');
  background-repeat: no-repeat;
  background-position: right 10px center;
  background-size: 15px;
}
