form label {
  font-size: 16px;
  font-weight: 700;
  font-family: inherit;
  margin-bottom: 2px;
}
form label.required:after {
  content: "*";
  color: var(--danger-100, #DC3545);
}
form label.forCheckOrRadio {
  font-weight: 400;
}
form .wicket-form-help-text, form p.wicket-form-help-text {
  margin: 5px 0px 0px;
  font-size: 14px;
  color: var(--dark-070, #656A6F);
}
form .wicket-form-help-text.forCheckOrRadio, form p.wicket-form-help-text.forCheckOrRadio {
  margin: 0px 20px 0px;
}
form .wicket-form-help-text.error, form p.wicket-form-help-text.error {
  color: var(--danger-100, #DC3545);
}
form .wicket-form-help-text.hidden, form p.wicket-form-help-text.hidden {
  display: none;
}
form.wicket-form {
  display: flex;
  flex-direction: column;
}
form.wicket-form .wicket-form-field-wrapper {
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
}